import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';


@Injectable({
    providedIn: 'root'
})
export class AuthService {

    isAuth: Boolean = false;
    isAuthSubject = new Subject<Boolean>();

    emitIsAuth(){
        this.isAuthSubject.next(this.isAuth);
    }

    constructor(private httpClient: HttpClient) { }

    connectUser(pseudo: String, password: String) {
        const compte = {
            pseudo: pseudo,
            password: password
        };
        return new Promise(
            (resolve, reject) => {
                this.httpClient
                    .post('http://localhost:8878/mini-jeu/php/connexion.php', compte)
                    .subscribe(
                        (response) => {
                            resolve(response);
                            if(response === "ok"){
                                this.isAuth = true;
                            }else{
                                this.isAuth = false;
                            }
                            this.emitIsAuth();
                        },
                        (error) => {
                            reject(error);
                        }
                    );
            }
        );
        
    }

    addUser(pseudo: String, email: String, password: String) {
        const compte = {
            pseudo: pseudo,
            email: email,
            password: password
        };
        return new Promise(
            (resolve, reject) => {
                this.httpClient
                    .post('http://localhost:8878/mini-jeu/php/register.php', compte)
                    .subscribe(
                        (response) => {
                            resolve(response);
                        },
                        (error) => {
                            reject(error);
                        }
                    );
            }
        );
    }

    disconnectUser(){
        this.isAuth = false;
        this.emitIsAuth();
    }
}