import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/service/auth.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {

  signupForm: FormGroup;
  validationCompte: boolean;
  isHidden: boolean = true;

  constructor(private authService: AuthService, private formBuilder: FormBuilder, private router: Router) { }

  ngOnInit(): void {
    this.initForm();
  }

  initForm() {
    this.signupForm = this.formBuilder.group({
      pseudo: ['', Validators.required],
      email: ['', [Validators.email, Validators.required]],
      password: ['', [Validators.required, Validators.pattern(/[0-9a-zA-Z]{6,}/)]]
    });
  }

  onSubmit() {
    this.isHidden = false;
    console.log(this.validationCompte);
    const pseudo = this.signupForm.get('pseudo').value;
    const email = this.signupForm.get('email').value;
    const password = this.signupForm.get('password').value;

    console.log('Go envoyer les données au php')
    this.authService.addUser(pseudo, email, password).then(
      (response) => {
        if (response !== "") {
          if (response.toString() === 'ok') {
            this.isHidden = false;
            this.validationCompte = true;
            this.router.navigate(['/auth/signin']);
          } else {
            console.log(this.validationCompte);
            this.isHidden = false;
            this.validationCompte = false;
          }
        }
      },
      (error) => {
        console.log(error);
      }
    );

    console.log('fini d\'envoyer au php');
  }

}
